.become-host-main-wrapper {
    display: flex;
    height: 100vh;
    overflow: hidden;

    .side-wrapper {
        flex-basis: 50%;
        display: flex;
        align-items: center;
        background: rgb(255, 56, 92);
        background: linear-gradient(356deg, rgba(255, 56, 92, 1) 56%, rgba(255, 255, 255, 1) 100%);
        padding: 40px;

        .questions-con {
            >* {
                font-size: rem(50px);
                max-width: 450px;
                color: $clr2;
                font-weight: bold;
            }

        }
    }

    .details-editor-wrapper {
        flex-basis: 50%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        padding: 10px;
        overflow-y: hidden;
        padding: 40px;

        .hompage-link {
            display: flex;
            align-items: center;
            justify-content: center;
            align-self: flex-end;
            background-color: $clr8;
            color: $clr3;
            font-size: rem(13.5px);
            border-radius: 30px;
            width: 150px;
            height: 30px;
        }

        .property-type-con {
            display: flex;
            flex-direction: column;
            gap: 15px;
            margin: auto;

            >* {
                padding: 20px;
                border: 2px solid $clr6;
                border-radius: 11px;
                transition: 0.2s;
                width: 350px;
                font-size: rem(20px);
                font-weight: 600;
            }

            .active {
                border: 2px solid $clr3;

            }

            >*:hover {
                border: 2px solid $clr3;
                cursor: pointer;
            }
        }

        .room-type-con {
            display: flex;
            flex-direction: column;
            gap: 20px;
            padding: 20px;
            margin: auto;

            >* {
                padding: 20px;
                border: 2px solid $clr6;
                border-radius: 11px;
                transition: 0.2s ease-in;
                width: 350px;
                font-size: rem(20px);
                font-weight: 600;

            }

            .active {
                border: 2px solid $clr3;

            }

            >*:hover {
                border: 2px solid $clr3;
                cursor: pointer;
            }
        }

        .capacity-con {
            display: flex;
            flex-direction: column;
            width: 450px;
            gap: 50px;
            margin: auto;

            >* {
                display: flex;
                justify-content: space-between;
                align-items: center;

            }

            .value-title {
                font-size: rem(28px);
                font-weight: 600;
                letter-spacing: 0.05rem;
            }

            .nums-con {
                display: flex;
                align-items: center;
                gap: 15px;

                span {
                    font-size: rem(18px);
                    color: $clr3;
                    font-weight: 600;
                }

                .btn {
                    display: grid;
                    align-items: center;
                    width: 32px;
                    height: 32px;
                    border: 1px solid $clr5;
                    border-radius: 50%;
                    font-size: rem(25px);
                    color: $clr4;
                    background-color: $clr2;
                    cursor: pointer;

                    &:hover {
                        border: 1px solid $clr3;
                        color: $clr3;
                    }

                    &:disabled {
                        color: $clr6;
                        border: 1px solid $clr6;
                    }
                }
            }
        }

        .address-con {
            margin: auto;

            form {
                display: flex;
                flex-direction: column;
                gap: 40px;

                input {
                    width: 300px;
                    height: 70px;
                    border-radius: 17px;
                    background-color: $clr2;
                    outline: none;
                    border: 1px solid $clr5;
                    font-size: rem(20px);
                    padding-inline-start: 20px;

                    &::placeholder {
                        font-size: rem(20px);
                    }
                }
            }
        }

        .amenities-con {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            gap: 20px;
            overflow-y: auto;

            >* {
                border: 2px solid $clr6;
                border-radius: 11px;
                transition: 0.2s;
                width: 140px;
                height: 140px;
                padding: 10px;
                cursor: pointer;
            }

            .active {
                border: 2px solid $clr3;
            }
        }

        .drop-warapper {
            width: 100%;
            margin: 5px 0px 50px 0px;

            .drop-con {
                overflow-y: scroll;
                border: 1px dashed $clr5;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 20px;
                height: 80vh;

                .do-it {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }

                .drop-gallery-wrapper {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-template-rows: 1fr 1fr 1fr;
                    width: 600px;
                    height: 700px;
                    column-gap: 10px;
                    row-gap: 12px;

                    .img-con {
                        position: relative;
                        width: 100%;
                        height: 200px;
                        outline: 1px dashed $clr5;

                        .img-menu-icon {
                            position: absolute;
                            top: 10px;
                            right: 10px;
                            background-color: rgba(255, 255, 255, 0.589);
                            border-radius: 50%;
                            cursor: pointer;

                        }

                        &.cover {
                            grid-column: 1/3;
                            width: 100%;
                            height: 400px;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }

                        &.full {
                            outline: none;
                        }
                    }
                }

            }

        }

        .listing-title-con {
            display: flex;
            flex-direction: column;
            gap: 15px;
            margin: auto;

            .listing-title {
                font-size: rem(23px);
                color: $clr3;
                font-weight: 600;
            }

            .listing-subtitle {
                color: $clr4;
                font-size: rem(18px);
            }

            textarea {
                resize: none;
                border: 1px solid $clr5;
                border-radius: 10px;
                outline: none;
                width: 100%;
                height: 200px;
                font-size: rem(40px);
                padding: 15px;
                font-family: 'AirLight';
                font-weight: bold;

                &::placeholder {
                    font-size: rem(40px);
                }
            }
        }

        .description-con {
            margin: auto;
            display: flex;
            flex-direction: column;
            gap: 25px;

            .description-title {
                font-size: rem(25px);
                color: $clr3;
                font-weight: 600;
            }

            textarea {
                resize: none;
                border: 1px solid $clr5;
                border-radius: 10px;
                outline: none;
                width: 500px;
                height: 200px;
                font-size: rem(20px);
                padding: 15px;
                font-family: 'AirLight';
            }
        }

        .pricing-con {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 53px;
            margin: auto;

            .setting-price {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 20px;

                .input-con {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    padding: 20px;

                    input[type=number] {
                        width: 300px;
                        height: 120px;
                        text-align: center;
                        color: $clr3;
                        font-size: rem(65px);
                        border-radius: 10px;
                        outline: none;
                        border: 2px solid $clr5;

                        &:focus {
                            border: 2px solid $clr3;
                        }

                    }

                    input::-webkit-outer-spin-button,
                    input::-webkit-inner-spin-button {
                        -webkit-appearance: none;
                        margin: 0;
                    }
                }

                .btn {
                    display: grid;
                    align-items: center;
                    width: 50px;
                    height: 50px;
                    border: 1px solid $clr5;
                    border-radius: 50%;
                    font-size: rem(25px);
                    color: $clr4;
                    background-color: $clr2;
                    cursor: pointer;

                    &:hover {
                        border: 1px solid $clr3;
                        color: $clr3;
                    }

                    &:disabled {
                        color: $clr6;
                        border: 1px solid $clr6;
                    }
                }
            }

            .info {
                width: 50%;
                font-size: rem(19px);
                text-align: center;
                font-weight: 600;
                letter-spacing: rem(0.5px);
            }

            .tips-con {
                width: 80%;
                border: 1px solid $clr6;
                border-radius: 15px;
                padding: 20px;
                font-size: rem(16px);
                font-weight: 600;
            }
        }

        .categories-con {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            gap: 20px;
            column-gap: 20px;
            overflow-y: auto;
            margin: auto;

            >* {
                border: 2px solid $clr6;
                border-radius: 11px;
                transition: 0.2s;
                width: 140px;
                height: 140px;
                padding: 10px;
                cursor: pointer;
            }

            .active {
                border: 2px solid $clr3;
            }
        }


        .preview-listing-wrapper {
            width: 350px;
            border: 1px solid $clr6;
            border-radius: 20px;
            overflow: hidden;
            padding: 10px;
            margin: auto;

            .preview-img-con {
                width: 100%;
                border-radius: 10px;

                img {
                    width: 100%;
                    height: 200px;
                    aspect-ratio: 1/1;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }

            .preview-header {
                padding: 10px;
                border-bottom: 1px solid $clr6;
            }

            .preview-property {
                display: flex;
                justify-content: space-between;
                padding: 10px;
                border-bottom: 1px solid $clr6;

                span {
                    width: 70%;
                    font-weight: 600;
                    font-size: rem(18px);
                }

                .avatar-con {
                    width: 30px;
                    height: 30px;
                    overflow: hidden;
                    border-radius: 50%;

                    img {
                        width: 100%;
                        object-fit: cover;
                    }
                }
            }

            .preview-capacity {
                display: flex;

                .proprietary-info-con {
                    width: 100%;
                    padding: 10px;
                    border-bottom: 1px solid $clr6;
                    font-weight: 600;

                }

            }

            .preview-desc {
                padding: 10px;
                border-bottom: 1px solid $clr6;
            }

            .preview-location {
                padding: 10px;
            }

            .btn-save-listing {
                background-color: $clr1;
                color: $clr1;
                padding: 5px;
            }
        }

        .paging-wrapper {
            position: fixed;
            bottom: 0;
            right: 0;
            z-index: 2;
            width: 50%;

            progress {
                accent-color: $clr3;
                height: 8px;
                width: 100%;
                transition: 0.4s ease-in;

            }

            .paging-btns-con {
                display: flex;
                justify-content: space-between;
                padding: 10px;
                background-color: $clr2;

                .btn-back {
                    border: none;
                    background-color: transparent;
                    font-size: rem(18px);
                    text-decoration: underline;
                    cursor: pointer;
                }

                .btn-next {
                    background-color: $clr3;
                    color: $clr2;
                    font-size: rem(16px);
                    font-weight: 600;
                    line-height: 20px;
                    width: 70px;
                    height: 42px;
                    border-radius: 8px;
                    cursor: pointer;
                }

                .btn-save-listing {
                    background-color: $clr1;
                    color: $clr2;
                    font-weight: 600;
                    padding: 15px;
                    border-radius: 8px;
                    cursor: pointer;
                    outline: none;
                    border: none;
                }
            }

        }
    }


    .side-con {
        flex-basis: 50%;
        background-color: black;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 40px;
        gap: 30px;

        >* {
            font-size: rem(60px);
            max-width: 550px;
            color: $clr2;
            font-size: 'AirMedium';
            font-weight: bold;
        }

        .btn-container {
            position: relative;
            width: 150px;
            height: 48px;
            padding: 0 0;
            margin: 0 0;
            cursor: pointer;
            border: none;
            display: grid;
            grid-template: repeat(10, 1fr) / repeat(10, 1fr);

            @for $i from 0 to 10 {
                .cell:nth-child(10n + #{$i + 1}):hover~.content {
                    --positionX: #{$i};
                }

                .cell:nth-child(n + #{10 * $i + 1}):nth-child(-n + #{10 * ($i + 1)}):hover~.content {
                    --positionY: #{$i};
                }
            }
        }

        .cell {
            width: 100%;
            height: 100%;
            z-index: 1;
        }

        .content {
            --positionX: 0;
            --positionY: 5;

            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            border-radius: 8px;

            background-image: radial-gradient(circle at calc((0 + var(--positionX, 0)) * 10%) calc((0 + var(--positionY, 0)) * 5%),
                    rgb(241, 37, 75) 0%,
                    rgb(230, 30, 77) 27.5%,
                    rgb(227, 28, 95) 40%,
                    rgb(215, 4, 102) 57.5%,
                    rgb(189, 30, 89) 75%,
                    rgb(189, 30, 89) 100%);
        }

        .action-btn {
            padding: 0 0;
            margin: 0 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            border: none;
        }

        span {
            padding: 0 0;
            margin: 0 0;
            color: white;
            font-size: 16px;
            line-height: 20px;
            font-weight: 600;
            font-family: sans-serif;
        }

    }

    .video-wrapper {
        flex-basis: 50%;

        video {
            width: 100%;
            object-fit: cover;
        }
    }
}